import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { Observable } from 'rxjs/Observable';
import { FilterPipe } from '../core/filter.pipe';
import { Upload } from '../models/upload';
import * as firebase from 'firebase/app';
import * as _ from "lodash";

@Component({
  selector: 'app-edit-posts',
  templateUrl: './edit-posts.component.html',
  styleUrls: ['./edit-posts.component.css']
})

export class EditPostsComponent implements OnInit {
  
  user: Observable<firebase.User>;
  postsDB: FirebaseListObservable<any[]>;
  msgVal: string = '';
  theresItems: number = 1 //1: waiting, 0: no items, 2: theres items
  posts: any[];
  search = "";
  postToUpdate: string;
  updateble: boolean[] = [];
  updated: boolean[] =[];
  isModalOpen: boolean = false;
  files: FileList;
  noFile: boolean;
  _listIndex: number;
  basePath: string = 'uploads/';
  selectedPost: any = {
    files: [""],
    title: "",
    description: ''
  }
  progressBarWidth: string = '0%';
  currentUpload: Upload;
  isSending: boolean = false;
  selectedFilesCount: number = 0;
  deletePopUp: boolean = false;
  postForDelete: any = {
    $key: ""
  }
  
  constructor(public af: AngularFireDatabase) { 
  	this.postsDB = af.list('/posts');
    this.postsDB.subscribe(resp => {
      this.posts = resp.sort(function(a, b){
          return b.date - a.date
      })
      
      for(let post of this.posts) {
        this.updateble.push(false);
        this.updated.push(false);
      }

      this.theresItems = (resp.length) ? 2 : 0
      this.selectedPost = this.posts[0]
    });
  }

  pushUpload(post: any, upload: Upload, index: number, total: number){
    let storageRef = firebase.storage().ref();
    let uploadTask = storageRef.child(`${this.basePath}/${upload.file.name}`).put(upload.file);

    uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
      ( snapshot ) => {
        upload.progress =  (uploadTask.snapshot.bytesTransferred / uploadTask.snapshot.totalBytes) * 100;
        this.progressBarWidth = (this.currentUpload.progress) + '%';
      },
      ( error ) => {
        console.log(error)
        console.log('Erroring');
      },
      () => {
        upload.url = uploadTask.snapshot.downloadURL;
        upload.name = upload.file.name
        this.saveFileData(post, upload)
      })
  }

  private saveFileData(post: any, upload: Upload) {
    let finalPost = post
    if(finalPost.files[0] == "") finalPost.files = []
    finalPost.files.push({"name": upload.name, "path": upload.url})
    this.postsDB.update(finalPost.$key, {files: finalPost.files}).then(e => {
      this.isSending = false
      this.updateble[this._listIndex] = false;
      setTimeout(() => { this.updated[this._listIndex] = false }, 1000)
    })
  }

  updateCurrentPost(postKey: string, index: number) {
    this.postToUpdate = postKey;
    this.updateble[index] = true;
  }

  upload(post: any) {
    if(this.selectedFilesCount > 1){
      let files = this.files
      let filesIndex = _.range(files.length)
      _.each(filesIndex, (idx) => {
        this.currentUpload = new Upload(files[idx]);
        this.pushUpload(post, this.currentUpload, idx, this.selectedFilesCount)});
    }
    else{
      let file = this.files.item(0)
      this.currentUpload = new Upload(file);
      this.pushUpload(post, this.currentUpload, 0, 1)
    }
  }

  updatePost(post: any, index: number) {
    if(typeof this.files !== 'undefined' && index == this._listIndex) {
      if(this.files.length) {
        this.isSending = true
        this.upload(post)
      }
    } else {
      this.postsDB.update(post.$key, post).then(() => {
        this.updated[index] = true;
        this.updateble[index] = false;
        setTimeout(() => { this.updated[index] = false }, 1000)
      })      
    }
  }

  detectFiles(files) {
    this.files = files
    if(files.lenght == 0) this.updateble[this._listIndex] = false
  }

  deleteFile(file: string, selectedPost: any, fileIndex: number, listIndex: number) {
    this.selectedPost = selectedPost
    let postIndex = 0
    
    for(var i = 0; i < this.posts.length; i++)
      if (selectedPost.$key == this.posts[i].$key)
        postIndex = i

    this.posts[postIndex].files.splice(fileIndex, 1);
    this.updateble[listIndex] = true;      
    this.selectedPost.files.splice(postIndex, 1);
    if(this.selectedPost.files.length == 0) this.selectedPost.files = [""]
  }

  setUpdatePost(post: any, listIndex: number){
    this.selectedPost = post
    this._listIndex = listIndex
    this.updateble[this._listIndex] = true;      
  }

  openModal(post: any){
    this.isModalOpen = true
    this.selectedPost = post
  }

  prepareForDelete(post: any){
    this.deletePopUp = true
    this.postForDelete = post
  }

  removePost(post: any){
    this.postsDB.remove(post.$key).then(_ => {
      this.deletePopUp = false
    })
  }

  ngOnInit() {
  }

}



