import { Component, OnInit } from '@angular/core';
import { AuthService } from "../core/auth.service";
import { AngularFireAuth } from 'angularfire2/auth';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

	userData = {
		email: "",
		password: ""
	}

  isValidated: boolean = true;
  validationMessage: string = ""
  isLoading: boolean = false;

  constructor(private auth: AuthService, private router: Router, private afAuth: AngularFireAuth) {
    this.afAuth.authState.subscribe((auth) => {
      if(auth){
        router.navigateByUrl('/dashboard');
      }
    });
  }

  async login() {
    this.isLoading = true;
  	if(await this.auth.emailLogin(this.userData.email, this.userData.password)){
      this.router.navigateByUrl('/dasboard');
      this.isLoading = false;
    }else{      
      this.validationMessage = "Wrong email or password, please try again";
      this.isValidated = false;
      this.isLoading = false;
      setTimeout(() => { this.isValidated = true }, 5000);      
    }
  }


  ngOnInit() {
  }

}



