import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';

@Component({
  selector: 'app-users-modal',
  templateUrl: './users-modal.component.html',
  styleUrls: ['./users-modal.component.css']
})
export class UsersModalComponent implements OnInit {

	@Output() modalSender: EventEmitter<boolean> = new EventEmitter<boolean>();
	@Output() usersSender: EventEmitter<any[]> = new EventEmitter<any[]>();

	isModalOpen: boolean = true
	usersBD: FirebaseListObservable<any[]>;
	users: any[];
	checkedArray: boolean[] = [];
	totalSelected: number = 0;
	selectedUsers: any[] = [];

	closeModal(){
		this.isModalOpen = false;
		this.modalSender.emit(this.isModalOpen)
		this.selectedUsers = [];
		this.totalSelected = this.selectedUsers.length
	}

	selectAll(){
		for(let i = 0; i <= this.checkedArray.length - 1; i++) 
			this.checkedArray[i] = true;
		this.totalSelected = this.checkedArray.length;
	}

	sendSelection(){
		let json = {};
		for(let i = 0; i <= this.checkedArray.length - 1; i++)
			if(this.checkedArray[i]) {
				this.selectedUsers.push(this.users[i])
			}
		this.isModalOpen = false;
		this.modalSender.emit(this.isModalOpen);
		this.usersSender.emit(this.selectedUsers)
	}

	updateUsers(index: number){
		this.checkedArray[index] = !this.checkedArray[index];
		this.totalSelected = 0;
		for(let value of this.checkedArray)
			if(value) this.totalSelected++;
	}

	humanDate(timeStamp: number){
		let fullDate = ""+new Date(timeStamp);
		let arrayDate = fullDate.split(' ');
		let formatedDate = `${arrayDate[2]} ${arrayDate[1]} ${arrayDate[3]} at ${arrayDate[4]}`
		return formatedDate;
	}

	constructor(public af: AngularFireDatabase) {
		this.usersBD = af.list('/users', {query: {
				orderByChild: "type",
				equalTo: "client"
			}
		});
		this.usersBD.subscribe(resp => {
			this.users = resp;
	  		for(let i = 0; i <= this.users.length - 1; i++) 
	  			this.checkedArray[i] = false
		});
	}

	ngOnInit() {
	}

}



