import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { AuthService } from "../core/auth.service";
import { AngularFireAuth } from 'angularfire2/auth'

@Component({
  selector: 'app-new-client',
  templateUrl: './new-client.component.html',
  styleUrls: ['./new-client.component.css']
})
export class NewClientComponent implements OnInit {

	passwordRepeat: string;
	password: string;

	userData = {
		email: "",
		nickName: "",
		type: "client",
		date: Date.now(),
		readedSections: {
    		calendar: true,
    		notification: true,
    		desktop: true,
    		service: true,
    		advantage: true,
    		rest_toom: true
		},
		posts: {
			calendar: [""],
			service: [""],
			rest_room: [""]
		},
		notifications: "on"
	}

	validationMessage: string;
	isValidated: boolean = true;
	usersBD: FirebaseListObservable<any[]>;
	isSending: boolean = false;
  	isPositiveMessage: boolean = false;

	constructor(public af: AngularFireDatabase, private auth: AuthService ) {
		this.usersBD = af.list('/users');
	}

	validate(){
		if(this.userData.email == "")
			this.validationMessage = "Nombre de usuario obligatorio";
		else if(this.password == "")
			this.validationMessage = "Contraseña obligatoria";
		else if(this.password.length < 7)
			this.validationMessage = "La contraseña debe tener 8 o más caracteres";
		else if(this.passwordRepeat == "")
			this.validationMessage = "Repetir contraseña es obligatorio";
		else if(this.passwordRepeat != this.password)
			this.validationMessage = "Las contraseñas no coinciden"
	  	else{
	  		this.isSending = true
	  		this.auth.emailSignUp(this.userData.email, this.password).then((a) => {
		  		var lastUser = this.af.list("/users", { query: {
		  			orderByChild: "email",
		  			equalTo: this.userData.email,
		  		}})

		  		lastUser.subscribe(res => {
		  			console.log(res[0].$key)
		  			this.usersBD.update(res[0].$key, this.userData).then(()=>{
			        this.isSending = false
			        this.userData = {
						email: "",
						nickName: "",
						type: "client",
						date: Date.now(),
						readedSections: {
				    		calendar: true,
				    		notification: true,
				    		desktop: true,
				    		service: true,
				    		advantage: true,
				    		rest_toom: true
						},
						posts: {
							calendar: [""],
							service: [""],
							rest_room: [""]
						},
						notifications: "on"
					}
			        this.validationMessage = "Usuario creado con éxito";
			        this.isPositiveMessage = true
			        this.isValidated = false;
			        setTimeout(() => { this.isValidated = true; this.isPositiveMessage = false }, 5000)		  				
			    	})
	      		})
	      	})
	  		return
	  	}
		this.isValidated = false
		setTimeout(() => { this.isValidated = true }, 5000);
	}

	ngOnInit() {
	}

}



