import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { UploadService } from '../core/upload.service';
import { Upload } from '../models/upload';
import * as firebase from 'firebase/app';

@Component({
  selector: 'app-edit-desktop',
  templateUrl: './edit-desktop.component.html',
  styleUrls: ['./edit-desktop.component.css']
})
export class EditDesktopComponent implements OnInit {

	desktopFile: FileList;
	desktopImage: string;
	desktopDB: FirebaseListObservable<any[]>;
	storageRef: any;
	progressBarWidth: string = '0%';
	basePath: string = 'uploads/';
	currentUpload: Upload;
	finalUrls: any[] = [];
	isSending: boolean = false;
	noFile: boolean = true

  constructor(public af: AngularFireDatabase) { 
  	this.desktopDB = af.list('/desktop');
    
    this.desktopDB.subscribe((snap) => {
      this.desktopImage = snap[0].$value
    })

		this.storageRef = firebase.storage().ref();
  }

  pushUpload(upload: Upload){
    
    let uploadTask = this.storageRef.child(`${this.basePath}/${upload.file.name}`).put(upload.file);
    uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
      ( snapshot ) => {
        upload.progress =  (uploadTask.snapshot.bytesTransferred / uploadTask.snapshot.totalBytes) * 100;
        this.progressBarWidth = (this.currentUpload.progress) + '%';
      },
      ( error ) => {
        console.log(error)
        console.log('Erroring');
      },
      () => {
        upload.url = uploadTask.snapshot.downloadURL;
        upload.name = upload.file.name
        this.saveFileData(upload)
        this.finalUrls.push({name: upload.file.name, path: upload.url})
      })
  }

  uploadFile() {
  	this.isSending = true
    let file = this.desktopFile
    this.currentUpload = new Upload(file.item(0));
    this.pushUpload(this.currentUpload)
  }

  detectFiles(file) {
    this.desktopFile = file
    this.noFile = false
  }

  private saveFileData(upload: Upload) {
    this.isSending = false
    firebase.database().ref().update({"desktop": {"value": upload.url}})
  }

  ngOnInit() {
  }

}



