import { Component, OnInit } from '@angular/core';
import { AuthService } from '../core/auth.service'
import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { Router } from '@angular/router';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  adminEmail = ''

  constructor(private auth: AuthService, private router: Router, private afAuth: AngularFireAuth, private db: AngularFireDatabase) {
    db.list('/admin').subscribe(res => {
      this.adminEmail = res[0].$value;
      if(this.adminEmail != auth.currentUser.email){
        auth.signOut()
      }
    })
    afAuth.authState.subscribe((auth) => {
      if(!auth){
        router.navigateByUrl('/login')
      }
    });
  }

  ngOnInit() {
  }

}


