import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { AuthService } from '../core/auth.service'
import { AngularFireAuth } from 'angularfire2/auth';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css']
})
export class SettingsComponent implements OnInit {

	passwordData = {
		newPassword: "",
		newPasswordRepeat: ""
	}

	validationMessage: string;
	isValidated: boolean = true;
	isSending: boolean = false;
 	isPositiveMessage: boolean = false;
  currentUser: any;

	constructor(public af: AngularFireDatabase, private afAuth: AngularFireAuth) {
	    afAuth.authState.subscribe((auth) => {
	      this.currentUser = auth;
	    });
	}

	validate(): void{
		if(this.passwordData.newPassword == "" &&
			this.passwordData.newPasswordRepeat == "")
			this.validationMessage = "All fields are required";
		else if(this.passwordData.newPassword != this.passwordData.newPasswordRepeat)
			this.validationMessage = "New passwords are not equal";
	  	else{
	  		let self = this;
	  		this.isSending = true
	  		let user = this.currentUser;
	  		  user.updatePassword(this.passwordData.newPassword).then(function() {
			    	self.isSending = false
		        self.passwordData = {
							newPassword: '',
							newPasswordRepeat: ''
						}
		        self.validationMessage = "Password successfully updated!!";
		        self.isPositiveMessage = true
		        self.isValidated = false;
		        setTimeout(() => { this.isValidated = true; this.isPositiveMessage = false }, 5000)
			  }).catch(function(error) {
		        self.validationMessage = "There was an error trying to update your password";
		        self.isPositiveMessage = false
		        self.isValidated = false;
		        setTimeout(() => { this.isValidated = true; this.isPositiveMessage = false }, 5000)
			  });
	  		return
	  	}
		this.isValidated = false
		setTimeout(() => { this.isValidated = true }, 5000);
	}

	ngOnInit() {
	}

}



