import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';

@Component({
  selector: 'app-advantages',
  templateUrl: './advantages.component.html',
  styleUrls: ['./advantages.component.css']
})

export class AdvantagesComponent implements OnInit {

	validationMessage: string;
	isHidden: boolean = false;
	isPositiveMessage: boolean = false;
	mainDB: FirebaseListObservable<any[]>;
	isLoaded: boolean = false
  
  constructor(public af: AngularFireDatabase) { 
  	this.mainDB = af.list("/")
  	af.list("/advantages").subscribe(resp => {
  		let myContainer = <HTMLElement> document.querySelector(".nicEdit-main");
  		this.isLoaded = true
  		myContainer.innerHTML = resp[0].$value
  	})
  }

  ngOnInit() {
  }

  saveHTML(){
  	this.isPositiveMessage = true
  	this.validationMessage = "Advantages section updated "
  	this.isHidden = false
  	let myContainer = <HTMLElement> document.querySelector(".nicEdit-main");
  	this.mainDB.update("advantages",{value: `${myContainer.innerHTML}`}).then(() => {
			setTimeout( ()=> { this.isHidden = true }, 2000)
  	})
  }

}



