import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { UploadService } from '../core/upload.service';
import { Upload } from '../models/upload';
import * as _ from "lodash";
import * as firebase from 'firebase';

@Component({
  selector: 'app-new-post',
  templateUrl: './new-post.component.html',
  styleUrls: ['./new-post.component.css']
})
export class NewPostComponent implements OnInit {

  types = {
    calendar: "Calendario",
    service: "Servicio",
    desktop: "Escritorio",
    rest_room: "Sala de espera",
    advantage: "Ventaja"
  }

  postData = {
		title: "",
		description: "",
    files: [],
    type: 'calendar',
    date: 0
	}

  clientData = {
    email: "",
    nickName: "",
    type: "client",
    date: "",
    readedSections: {
        calendar: true,
        notification: true,
        desktop: true,
        service: true,
        advantage: true
    },
    posts: {
      calendar: [""],
      service: [""],
      rest_room: [""]
    },
    title: ""
  }

  progressBarWidth: string = '0%';
  selectedFilesCount: number;
  selectedFiles: FileList;
  currentUpload: Upload;

	postsBD: FirebaseListObservable<any[]>;
  usersDB: FirebaseListObservable<any[]>;
	validationMessage: string;
  notificable: boolean = false;
	isValidated: boolean = true;
  isActive: boolean = false;
  isModalOpen: boolean = false
  isSending: boolean = false;
  isPositiveMessage: boolean = false;
  selectedUsers: any[] = []
  basePath: string = 'uploads/';
  finalUrls: any[] = [];

  constructor(public af: AngularFireDatabase, private uploader: UploadService) {
    this.postsBD = af.list('/posts');
    this.usersDB = af.list('/users');
  }

  pushUpload(upload: Upload, index: number, total: number){
    let storageRef = firebase.storage().ref();
    let uploadTask = storageRef.child(`${this.basePath}/${upload.file.name}`).put(upload.file);

    uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
      ( snapshot ) => {
        upload.progress =  (uploadTask.snapshot.bytesTransferred / uploadTask.snapshot.totalBytes) * 100;
        this.progressBarWidth = (this.currentUpload.progress) + '%';
      },
      ( error ) => {
        console.log(error)
        console.log('Erroring');
      },
      () => {
        upload.url = uploadTask.snapshot.downloadURL;
        upload.name = upload.file.name
        this.saveFileData(upload)
        this.finalUrls.push({name: upload.file.name, path: upload.url})
        
        if(!index) this.updateDatabase(this.finalUrls);
      })
  }

  private saveFileData(upload: Upload) {
    this.af.list(`${this.basePath}/`).push(upload);
  }

  detectFiles(files) {
    this.selectedFiles = files
    this.selectedFilesCount = files.length
  }

  upload() {
    if(this.selectedFilesCount > 1){
      let files = this.selectedFiles
      let filesIndex = _.range(files.length)
      _.each(filesIndex, (idx) => {
        this.currentUpload = new Upload(files[idx]);
        this.pushUpload(this.currentUpload, idx, this.selectedFilesCount)});
    }
    else{
      let file = this.selectedFiles.item(0)
      this.currentUpload = new Upload(file);
      this.pushUpload(this.currentUpload, 0, 1)
    }
  }

  ngOnInit() {
  }

  openModal(){
    this.isModalOpen = true
  }

  validate(){
  	if(this.postData.title == "")
  		this.validationMessage = "El título es obligatorio";
  	
  	else if(this.postData.description == "")
  	  this.validationMessage = "La descripción es obligatoria";

    else if(_.isEmpty(this.selectedUsers))
      this.validationMessage = "Debes seleccionar al menos a un destinatario";
  	
    else {
      this.isSending = true
      if(!this.selectedFilesCount){
        this.updateDatabase([""]);
      } else {
        this.upload();
      }
  	}

  	this.isValidated = false;
  	setTimeout(() => { this.isValidated = true }, 5000)
  }

  updateDatabase(finalUrls: string[]) {
    this.postData.files = finalUrls
    this.postData.date = new Date().getTime();
    this.postsBD.push(this.postData).then((snap) => {
      for(var i = 0; i < this.selectedUsers.length; i++) {
        (this.selectedUsers[i].posts[this.postData.type][0] == "") ? this.selectedUsers[i].posts[this.postData.type][0] = [] : null
        let user = {
          readedSections: this.selectedUsers[i].readedSections,
          posts: this.selectedUsers[i].posts,
          notifications: (this.notificable == true) ? "on" : "off",
          title: this.postData.title + " - [ " + this.types[this.postData.type] + " ] "
        }
        user.posts[this.postData.type].push({
          "readed": false,
          "key": snap.key,
          "notification":  this.notificable
        })

        user.readedSections[this.postData.type] = false
        if(this.notificable) user.readedSections["notification"] = false
        this.usersDB.update(this.selectedUsers[i], user).then(()=>{
          this.isSending = false
          this.postData = {
            title: '',
            description: '',
            files: [],
            type: '',
            date: 0
          }
          this.finalUrls = [];
          this.selectedFilesCount = 0
          this.validationMessage = "Post creado con éxito";
          this.isPositiveMessage = true;
          this.isValidated = false;
          setTimeout(() => { this.isValidated = true; this.isPositiveMessage = false }, 5000);
        })        
      }
      return;
    });
  }

}



