import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-files-modal',
  templateUrl: './files-modal.component.html',
  styleUrls: ['./files-modal.component.css']
})
export class FilesModalComponent implements OnInit {

	@Input() files: string[]
	@Output() modalSender: EventEmitter<boolean> = new EventEmitter<boolean>();
	@Output() filesSender: EventEmitter<any[]> = new EventEmitter<any[]>();

	isModalOpen: boolean = false	

	constructor() { }

	ngOnInit() {
	}

	editingDone(){
		this.modalSender.emit(this.isModalOpen);
		this.filesSender.emit(this.files)
	}

	closeModal(){
		this.isModalOpen = false;
		this.modalSender.emit(this.isModalOpen)
		this.files = []
	}

}




